import React, { useState } from 'react';
const moment = require('moment-business-days');


const stock_api = {
  key: "FYKD9_Xv14hjBbfSCa4Cg20BJZgaA2mC",
  base: "https://api.polygon.io/v1/open-close/"
}

function App() {

  const [query, setQuery] = useState('');
  const [response, setResponse] = useState({});

  const formatDate = date => {
      let d = new Date(date),
          month = '' + (d.getMonth() + 1),
          day = '' + d.getDate(),
          year = d.getFullYear();

      if (month.length < 2) 
          month = '0' + month;
      if (day.length < 2) 
          day = '0' + day;

      return [year, month, day].join('-');
  }

  const getPrevBusinessDay = (date) => {
      return moment(formatDate(date), 'YYYY-MM-DD').isBusinessDay() ? 
          moment(formatDate(date), 'YYYY-MM-DD').prevBusinessDay().format('YYYY-MM-DD') : 
          moment(formatDate(date), 'YYYY-MM-DD').prevBusinessDay().format('YYYY-MM-DD')
  }

  const search = event => {
    if (event.key === "Enter") {
      fetch(`${stock_api.base}${query}/${getPrevBusinessDay(new Date())}?adjusted=true&apiKey=${stock_api.key}`)
        .then(res => res.json())
        .then(result => {
          setResponse(result);
          setQuery('');
        });
    }
  }


  const dateBuilder = (d) => {
    let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];

    let day = days[d.getDay()];
    let date = d.getDate();
    let month = months[d.getMonth()];
    let year = d.getFullYear();

    return `${day}, ${date} ${month} ${year}`
  }

  return (
    //Ternary operator, changes background image to sunny
    <div className='app'>
      <main>
        <div className="search-box">
          <input 
            type="text"
            className="search-bar"
            placeholder="Enter a stock symbol..."
            onChange={e => setQuery(e.target.value)}
            value={query}
            onKeyPress={search}
          />
        </div>

        {(typeof response != "undefined") ? (
        <div>
          <div className="date">{dateBuilder(new Date())}</div>
          <div className="open-box">
            <span className="open">{`Open: `}</span>
            <span className="open">{`${(!response.open) ? '' : "$" + response.open}`}</span>
          </div>
          <div className="close-box">
            <span className="close">{`Close: `}</span>
            <span className="close">{`${(!response.close) ? '' : "$" + response.close}`}</span>
          </div>
        </div>
        ) : ('')}
      </main>
    </div>
  );
}

export default App;
